*{
    margin: 0px;
    padding: 0px;
    font-family: "Noto Sans", sans-serif;

}
.con1{
    height: 96vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.first_heading{
    margin-left: 100px;
}
.name{
    color: aliceblue;
    margin-top: 30px;
    font-size: 60px;
   
}
.first_heading_quote{
    color: aliceblue;
}
.demoweb{
    overflow: hidden;
    height: 90vh;
    width: 80vw;
   border-top: 4px solid white;
   border-left: 1px solid white;
   border-right: 1px solid white;
 border-radius: 5px;
    margin: 50px;
    background-color: rgb(44, 44, 44);

}
.typer{
    font-size: 16px;
    color: yellow;
}
.DemoHeader{
    height: 30px;
    width: 100%;
    background-color: black;
    
}
.headerTab{
    background-color: rgb(44, 44, 44);
padding: 5px 10px;
display: inline-block;
    color: rgb(217, 176, 99);
   
}
.icon{
/* border: 2px solid red; */
/* margin-top: 5px; */
float: right;
}
.ic{
    color: white;
    margin: 2px;
    height: 20px;
    width: 15px;
    margin: 0px 8px;
}

.err{
padding: 10px;
}
.err p{
   
    color:wheat;
    font-size: 16px;
    margin-bottom: 10px;
}
.err li{
    color:wheat;
    font-size: 16px;
    list-style: none;
    margin-left: 20px;
}
b{
    color: rgb(26, 161, 210);
}
big{
    color: rgb(113, 203, 113);
}

.terminal{
    /* border: 2px solid red; */
    height: 100%;
    width: 100%;
   border-top: 1px solid rgb(83, 73, 73);
    background-color: rgb(26, 20, 11);
    margin-top: 30px;
}
.terminalheader{
    display: flex;
  
}
.terminalheader li{
color: rgb(143, 143, 143);
list-style: none;
margin: 5px 10px;
}
@media (max-width: 700px) {
    .first_heading{
        margin-left: 30px;
    }
    .name{
      
        font-size: 30px;
       
    }
    .typer{
        font-size: 12px;
    }
    .err p{
        font-size: 12px;
    }
    .err li{
        font-size: 12px;
    }
   
    .terminalheader li{
      font-size: 12px;
        }

}